import React from "react";

import MyModal from "./parts/Modal/Modal";
import Images from "./parts/ImagesSection/Images";

import { ResultsPageContainer, ResultsPageFormContainer } from "./styled";
import { ErrorMessage, Form, HiddenH1 } from "components";

import { useClearOnMount } from "hooks";

const ResultsPage = () => {
    useClearOnMount();
    return (
        <ResultsPageContainer>
            <HiddenH1 text="Matching images" />
            <ErrorMessage />
            <ResultsPageFormContainer>
                <Form />
            </ResultsPageFormContainer>
            <MyModal />
            <Images />
        </ResultsPageContainer>
    );
};

export default ResultsPage;
